* {
  box-sizing: border-box;
}

body {
  background-color: #f0f0f0;
  background-image: url(../img/geometry.png);
  background-blend-mode: multiply;
  display: grid;
  @include bp(md) {
    grid-template-columns: $header-width 1fr;
  }
}

.content-wrap {
  padding: 1rem;
  position: relative;
  @include bp(md) {
    padding: 3rem;
  }

